import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconTextSvg from "../../react-svgs/IconText";

const IconText = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconTextSvg} ref={ref} {...props} />;
  }
);

export default IconText;
