import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconCopySvg from "../../react-svgs/IconCopy";

const IconCopy = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconCopySvg} ref={ref} {...props} />;
  }
);

export default IconCopy;
