import React, { useEffect, useState } from "react";

import { Input, message } from "antd";
import { useTitle } from "let-hooks/lib";
import styles from "./style.module.scss";
import CodeEditor from "../../tiny-code/Editor/components/CodeEditor";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { IconCopy } from "~/icons";
import Head from "next/head";

const FormatJSON = (props: { jsonStr: string }) => {
  const [result, setResult] = useState<string | undefined>();

  useEffect(() => {
    try {
      const result = JSON.parse(props.jsonStr);
      setResult(JSON.stringify(result, null, 2));
    } catch (err: any) {
      console.error(err);
      setResult(undefined);
    }
  }, [props.jsonStr]);

  const handleCopy = (_: string, result: boolean) => {
    if (result) {
      message.destroy();
      message.success("复制成功");
    }
  };

  return (
    <div className={styles["code-result"]}>
      <div className={styles.actions}>
        <CopyToClipboard text={result} onCopy={handleCopy}>
          <IconCopy className={styles["action-item"]} />
        </CopyToClipboard>
      </div>
      <div className={styles["format-result"]}>
        <CodeEditor value={result} readonly lang="json" theme="light" />
      </div>
    </div>
  );
};
const DateTimePage = () => {
  const [jsonStr, setJsonStr] = useState("");
  useTitle("JSON格式化");

  return (
    <div className={styles.container}>
      <Head>
        <meta name="keywords" content="JSON格式化,JSON" />
      </Head>
      <div className={styles.main}>
        <div className={styles["left-box"]}>
          <Input.TextArea
            className={styles["raw"]}
            placeholder="请输入json数据"
            value={jsonStr}
            onChange={(e) => setJsonStr(e.target.value)}
          />
        </div>
        <div className={styles["right-box"]}>
          <FormatJSON jsonStr={jsonStr} />
        </div>
      </div>
    </div>
  );
};

export default DateTimePage;
