import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconGithubSvg from "../../react-svgs/IconGithub";

const IconGithub = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconGithubSvg} ref={ref} {...props} />;
  }
);

export default IconGithub;
