import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconThemeSvg from "../../react-svgs/IconTheme";

const IconTheme = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconThemeSvg} ref={ref} {...props} />;
  }
);

export default IconTheme;
