import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconZitiSvg from "../../react-svgs/IconZiti";

const IconZiti = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconZitiSvg} ref={ref} {...props} />;
  }
);

export default IconZiti;
