import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconRectangleSvg from "../../react-svgs/IconRectangle";

const IconRectangle = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconRectangleSvg} ref={ref} {...props} />;
  }
);

export default IconRectangle;
