import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconDatabaseSvg from "../../react-svgs/IconDatabase";

const IconDatabase = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconDatabaseSvg} ref={ref} {...props} />;
  }
);

export default IconDatabase;
