import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconColorSvg from "../../react-svgs/IconColor";

const IconColor = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconColorSvg} ref={ref} {...props} />;
  }
);

export default IconColor;
