import React from "react";
import Icon from "iconfont-extract-icon";

import { IconBaseProps } from "iconfont-extract-icon/lib/types";
import IconSettingSvg from "../../react-svgs/IconSetting";

const IconSetting = React.forwardRef<HTMLSpanElement, IconBaseProps>(
  (props, ref): JSX.Element => {
    return <Icon component={IconSettingSvg} ref={ref} {...props} />;
  }
);

export default IconSetting;
